import React from 'react'
import { Box, TextInput, TextArea } from 'grommet'

function PostJobDescription(params) {
  const setDescription = function(event) {
    if (event.target.value.length <= 500) {
      params.setDescription(event.target.value)
    }
  }
  return (
    <div>
      <h3>Description</h3>
      <Box width='xlarge'>
        <Box margin='small'>
          <TextArea
            placeholder='Describe a little about the project your company/client is going through. Be concise. You only have 80 words!'
            value={params.description}
            onChange={setDescription}
            resize={false}
          />
        </Box>
        {params.description.length} / 500
      </Box>
      <h3>Job Responsibilities</h3>
      You can have up to five
      <Box width='xlarge'>
        <Box margin='small'>
          <TextInput value={params.responsibility1} onChange={event => params.setResponsibility1(event.target.value)} placeholder='Type job responsibility here' />
        </Box>
        <Box margin='small'>
          <TextInput value={params.responsibility2} onChange={event => params.setResponsibility2(event.target.value)} placeholder='Type job responsibility here' />
        </Box>
        <Box margin='small'>
          <TextInput value={params.responsibility3} onChange={event => params.setResponsibility3(event.target.value)} placeholder='Type job responsibility here' />
        </Box>
        <Box margin='small'>
          <TextInput value={params.responsibility4} onChange={event => params.setResponsibility4(event.target.value)} placeholder='Type job responsibility here' />
        </Box>
        <Box margin='small'>
          <TextInput value={params.responsibility5} onChange={event => params.setResponsibility5(event.target.value)} placeholder='Type job responsibility here' />
        </Box>
      </Box>
      <h3>Required Experience</h3>
      You can have up to five
      <Box width='xlarge'>
        <Box margin='small'>
          <TextInput value={params.experience1} onChange={event => params.setExperience1(event.target.value)} placeholder='Type previous experience here' />
        </Box>
        <Box margin='small'>
          <TextInput value={params.experience2} onChange={event => params.setExperience2(event.target.value)} placeholder='Type previous experience here' />
        </Box>
        <Box margin='small'>
          <TextInput value={params.experience3} onChange={event => params.setExperience3(event.target.value)} placeholder='Type previous experience here' />
        </Box>
        <Box margin='small'>
          <TextInput value={params.experience4} onChange={event => params.setExperience4(event.target.value)} placeholder='Type previous experience here' />
        </Box>
        <Box margin='small'>
          <TextInput value={params.experience5} onChange={event => params.setExperience5(event.target.value)} placeholder='Type previous experience here' />
        </Box>
      </Box>
    </div>
  )
}

export default PostJobDescription;