import React from 'react';
import { useParams } from 'react-router-dom';
import { Box, Grid, Tabs, Tab, Button, Grommet } from 'grommet'

import JobViewRecruiterSubSection from './job/JobViewRecruiterSubSection'
import JobViewExtraInfoSubSection from './job/JobViewExtraInfoSubSection'
import JobViewSocialSubSection from './job/JobViewSocialSubSection'
import JobTab from './job/JobTab'
import BenefitsTab from './job/BenefitsTab'
import CompanyTab from './job/CompanyTab'
import { css } from 'styled-components'

import { LightGrey } from './global/colors'
import { CustomizeTheme } from './global/styles'

const names = [
  'Jadon Kaufman',
  'Ellesse Bravo',
  'Claudia Sheldon',
  'Kelsi Huffman',
  'Jamelia Arroyo',
  'Armaan Barr',
  'Arielle Zavala',
  'Ronny Talley',
  'Federico Hooper',
  'Beverly O\'Reilly'
]

const pictures = [
  'https://cdn.fastly.picmonkey.com/contentful/h6goo9gw1hh6/2sNZtFAWOdP1lmQ33VwRN3/24e953b920a9cd0ff2e1d587742a2472/1-intro-photo-final.jpg?w=800&q=70',
  'https://widgetwhats.com/app/uploads/2019/11/free-profile-photo-whatsapp-4.png',
  'https://www.suelaceyphotography.com/v/p/QDTKd4AO9ofE9HwbZ5v5anz7-business-profile-shots-img-0241-1582334654.jpg',
  'https://i.insider.com/5899ffcf6e09a897008b5c04?width=600&format=jpeg&auto=webp',
  'https://sites.psu.edu/iabcpennstate/wp-content/uploads/sites/18140/2015/01/professional-woman-4.jpg',
  'https://cathleo.com.au/wp-content/uploads/2016/03/Profiles-UnisA-5.jpg',
  'https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcQsBvTHtysWxzGWd0e3yTig65uzlDf1ZsBUKoDhB-w7nkyVyOV9&usqp=CAU',
  'https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcSwNvj0HEnOYsY5Ny0qcuf1BDJXOEQnMEqHsHQC4ocbMR6FSwpP&usqp=CAU',
  'https://ocbj.media.clients.ellingtoncms.com/img/photos/2011/06/03/SR_Profile_Ken_Sullivan_6-6_t670.jpg?b3f6a5d7692ccc373d56e40cf708e3fa67d9af9d',
  'https://www.joscottimages.co.uk/_webedit/cached-images/263-0-0-893-10000-7451-612.jpg'
]

function Job(params) {
  let { id } = useParams();
  fetch('https://qi827wd4q4.execute-api.eu-west-1.amazonaws.com/api/sboj-job-function')
    .then(res => res.json())
    .then(
      (result) => {
        console.log(result)
      })

  const [index, setIndex] = React.useState();

  const theme = {
    tab: {
      active: {
        color: 'dark-1'
      },
      color: 'accent-1',
      border: {
        color: 'accent-1'
      },
      size: '250px',
      extend: ({ theme }) => css`
      font-weight: bold;
      width: 200px;
    `
    },
    tabs: {
      gap: 'medium',
    }
  }

  const onActive = nextIndex => setIndex(nextIndex);

  const randomName = names[Math.floor(Math.random() * names.length)];
  const randomPicture = pictures[Math.floor(Math.random() * pictures.length)];
  if (!params.job) {
    params = {}
    params.job = {
      company: {
        name: 'London Tech',
        website: 'http://www.LonTec.com',
        location: 'London, UK',
        techStack: [
          'Python',
          'Java',
          'Django'
        ],
        elevatorPitch: 'At London Tech, we solve the most complex problems with the most updated solutions.',
        info: 'Since 2010, London Tech, has been consulting with some of the most well known and forward thinking companies in the world. We have built a reputation for delivering complex solution in the simplest and most effective ways to make to that any technical problem is completely solved.',
        size: '+100',
        founded: '2010',
        profile_picture: 'https://i.imgur.com/rK5Jkkc.jpg',
        cover_picture: 'https://i.imgur.com/pazVpu2.jpg',
        socialMedia: {
          linkedin: 'https://www.linkedin.com/company/sboj-io/',
          twitter: 'https://twitter.com/CrosstownDough',
          instagram: 'https://www.instagram.com/crosstowndoughnuts'
        }
      },
      recruiter: {
        name: randomName,
        title: 'Head of Recruiting',
        organization: 'Sboj.io',
        rating: getRandomInt(1, 5),
        profile_picture: randomPicture
      },
      benefits: {
        salary: {
          currency: 'GBP',
          min: '40,000',
          max: '50,000'
        },
        remoteWorking: 'Yes',
        bonus: '5,000',
        bonusStructure: {
          'Company performance': 50,
          'Personal performance': 50
        },
        holiday: 28,
        equity: 'No',
        healthcare: 'Vitality - Full Memebership',
        other: [
          'Health Care (Private)',
          'Free Fruit and Coffee',
          'Travel loan',
          'Quarterly company parties',
          'Gym Membership',
        ]
      },
      position: 'Inhouse SAP ABAP Developer',
      description: 'A global end user in the Consulting industry is seeking a talented and professional ABAP developer for their HQ in the Cologne Area.',
      responsibilities: [
        'Software development in the SAP ERP environment with the aid of all available development environments',
        'Customisation of the functional SAP modules FI/CO',
        'Interface programming and form design with SAP Script / Smartforms',
        'Ensuring stability and availability of the SAP environment including the interfaces through standardised change and test management',
        'Provide 1st and 2nd level support'
      ],
      experiences: [
        '2-5 Years Cross-Module customisation Ideally with in FI/CO',
        'Excellent understanding of ABAP and ABAP OO as well as project management knowledge',
        'Able to work independently ',
        'A hunger to grow and learn newer technologies',
        'Conversational German and Fluent English'

      ],
      employment: 'Permanent',
      businessTravel: '0'
    }
  }

  const [buttonApplied, setButtonApplied] = React.useState(false);

  return (
    <div>
      <Box direction='row' height='auto'>
        <Box fill size='large'>
          <div>
            <h1>{params.job.position}</h1>
            <h2 style={{ color: LightGrey, marginBottom: 0 }}>{params.job.company.name}</h2>
          </div>
        </Box>
        <Box fill align='end'>
          {!params.hideApplyButton &&
            <Box width='medium' pad={{ top: 'large', right: 'large' }}>
              {!buttonApplied &&
                <Button primary size='large' label='Apply' onClick={() => { setButtonApplied(true) }} />
              }
              {buttonApplied &&
                <Button primary size='large' label='Applied' onClick={() => { }} />
              }
            </Box>
          }
        </Box>
      </Box>
      <Grid
        rows={['auto', 'flex']}
        columns={[['auto', '800px'], ['350px', '350px']]}
        areas={[
          { name: 'header', start: [0, 0], end: [1, 0] },
          { name: 'main', start: [0, 1], end: [0, 1] },
          { name: 'side', start: [1, 1], end: [1, 1] }
        ]}
      >
        <Box
          gridArea='header'
          direction='row'
          align='center'
          justify='between'
          pad={{ horizontal: 'small', vertical: 'small' }}
        >
        </Box>
        <Grommet theme={CustomizeTheme(theme)}>
          <Tabs activeIndex={index} onActive={onActive} justify='left'>
            <Tab title='The Job'>
              <Box gridArea='main'>
                <JobTab
                  description={params.job.description}
                  responsibilities={params.job.responsibilities}
                  experiences={params.job.experiences}
                  businessTravel={params.job.businessTravel}
                />
              </Box>
            </Tab>
            <Tab title='The Benefits'>
              <Box gridArea='main'>
                <BenefitsTab
                  benefits={params.job.benefits}
                />
              </Box>
            </Tab>
            <Tab title='The Company'>
              <Box gridArea='main'>
                <CompanyTab
                  company={params.job.company}
                />
              </Box>
            </Tab>
          </Tabs>
        </Grommet>
        <Box fill gridArea='side' justify='top' align='center' pad={{ horizontal: 'medium', vertical: 'none' }} margin={{ top: '38px' }}>
          <JobViewRecruiterSubSection
            recruiter={params.job.recruiter}
          />

          <JobViewExtraInfoSubSection
            website={params.job.company.website}
            techStack={params.job.company.techStack}
            location={params.job.company.location}
          />

          <JobViewSocialSubSection social={params.job.company.socialMedia} />
        </Box>
      </Grid>
    </div>
  );
}

function getRandomInt(min, max) {
  min = Math.ceil(min);
  max = Math.floor(max);
  return Math.floor(Math.random() * (max - min + 1)) + min;
}

export default Job;
