import React, { useState } from 'react'
import { Box, TextInput, Select, MaskedInput, RadioButton, FormField, RangeInput } from 'grommet'
import { FormClose } from 'grommet-icons'
import { LightGrey, Purple, White } from './global/colors'

function PostSalaryBenefits(params) {

  const [otherBenefitsInput, setOtherBenefitsInput] = useState('');
  const onKeyPressOtherBenefitsInput = function (event) {
    if (event.key === 'Enter') {
      let otherBenefits = [...params.otherBenefits]
      otherBenefits.push(otherBenefitsInput)
      params.setOtherBenefits(otherBenefits)
      setOtherBenefitsInput('')
    }
  }
  const onSelectOtherBenefitsInput = function (event) {
    let otherBenefits = [...params.otherBenefits]
    otherBenefits.push(event.suggestion)
    params.setOtherBenefits(otherBenefits)
    setOtherBenefitsInput('')
  }

  const [bonusInput, setBonusInput] = useState('');
  const onKeyPressBonusInput = function (event) {
    if (event.key === 'Enter') {
      let bonus = { ...params.bonusStructure }
      bonus[event.target.value] = 50
      params.setBonusStructure(bonus)
      setBonusInput('')
    }
  }

  const onChangeBonusStructureSlider = (key) => {
    return (event) => {
      let bonus = { ...params.bonusStructure }
      bonus[key] = event.target.value
      params.setBonusStructure(bonus)
    }
  }

  const deleteBonusStructure = (key) => {
    return (event) => {
      let bonus = { ...params.bonusStructure }
      delete bonus[key]
      params.setBonusStructure(bonus)
    }
  }

  return (
    <div>
      <h3>Salary</h3>
      <Box width='small'>
        <FormField label={<div style={{ fontWeight: 'bold', color: LightGrey }}>Currency</div>}>
          <Select
            options={['GBP', 'EUR', 'USD']}
            value={params.currency}
            onChange={({ option }) => params.setCurrency(option)}
            placeholder='Currency'
          />
        </FormField>
      </Box>
      <Box align='center' direction='row'>
        <Box width='small'>
          <FormField label={<div style={{ fontWeight: 'bold', color: LightGrey }}>From</div>}>
            <MaskedInput
              mask={[
                {
                  regexp: /^[0-9]{1,7}$/,
                  placeholder: "50000"
                },
              ]}
              value={params.minSalary}
              onChange={event => params.setMinSalary(event.target.value)}
            />
          </FormField>
        </Box>
        <div style={{ width: 20, border: 'solid 1px ' + LightGrey, margin: 10 }} />
        <Box width='small'>
          <FormField label={<div style={{ fontWeight: 'bold', color: LightGrey }}>To</div>}>
            <MaskedInput
              mask={[
                {
                  regexp: /^[0-9]{1,7}$/,
                  placeholder: "60000"
                },
              ]}
              value={params.maxSalary}
              onChange={event => params.setMaxSalary(event.target.value)}
            />
          </FormField>
        </Box>
      </Box>

      <h3>Bonus</h3>

      <Box width='small'>
        <TextInput
          value={params.bonus}
          onChange={event => params.setBonus(event.target.value)}
          placeholder='' />
      </Box>

      <Box width='medium'>
        <FormField label={<div style={{ fontWeight: 'bold', color: LightGrey }}>How is it achieved?</div>}>
          <TextInput
            value={bonusInput}
            onChange={event => setBonusInput(event.target.value)}
            onKeyPress={onKeyPressBonusInput}
            dropProps={{ height: "small" }}
            placeholder='Personal Performance, Stocks...' />
        </FormField>
      </Box>
      {params.bonusStructure && Object.entries(params.bonusStructure).map(([text, value]) => (
        <Box direction='row' margin='small'>
          <div
            style={{
              width: 150,
              textAlign: 'center',
              backgroundColor: Purple,
              color: White,
            }}>
            {text}
          </div>
          <FormClose pad={{ left: 'large' }} style={{ cursor: 'pointer' }} color='red' onClick={deleteBonusStructure(text)} />
          <Box size='small' margin={{ left: 'small', right: 'small' }}>
            <RangeInput
              value={value}
              onChange={onChangeBonusStructureSlider(text)}
            />
          </Box>
          <div>{value}%</div>
        </Box>
      ))}

      <h3>Equity</h3>
      <Box align='center' direction='row'>
        <Box margin='small'>
          <RadioButton
            label='Yes'
            name='equity'
            value='true'
            checked={params.equity === 'true'}
            onChange={event => params.setEquity(event.target.value)}
          />
        </Box>
        <Box margin='small'>
          <RadioButton
            label='No'
            name='equity'
            value='false'
            checked={params.equity === 'false'}
            onChange={event => params.setEquity(event.target.value)}
          />
        </Box>
      </Box>

      <h3>Healthcare</h3>
      <Box width='medium'>
        <Select
          options={['None', 'Vitality - Basic Membership', 'Vitality - Premium Membership', 'BUPA Cover', 'Other']}
          value={params.healthcare}
          onChange={({ option }) => params.setHealthcare(option)}
          placeholder='Currency'
        />
      </Box>

      <h3>Other Benefits</h3>
      <Box width='medium'>
        <TextInput
          value={otherBenefitsInput}
          onChange={event => setOtherBenefitsInput(event.target.value)}
          onSelect={onSelectOtherBenefitsInput}
          onKeyPress={onKeyPressOtherBenefitsInput}
          suggestions={otherBenefitSuggestions}
          dropProps={{ height: "small" }}
          placeholder='Personal Performance, Stocks...' />
      </Box>
      {params.otherBenefits && params.otherBenefits.map(text => (
        <p>{text}</p>
      ))}
    </div>
  );
}

export default PostSalaryBenefits;

const otherBenefitSuggestions = [
  'Free Fruit',
  'Free Coffee drinks',
  'Discounted meals',
  'Dental care plans',
  'Free travel',
  'Subsided travel',
  'Travel Loan',
  'Free company events',
  'Childcare vouchers',
  'Flexible working hours',
  'Gym Membership',
  'Yearly Company Holiday',
  'Xmas Food hamper',
  'Cycle Scheme',
  'Shopping Vouchers',
  'Personal Loans',
  'Course Allowance',
  'Car Allowance'
]