import React from 'react'
import { Grommet, Button } from 'grommet'
import { CustomizeTheme } from '../styles'
import { FormPreviousLink } from 'grommet-icons'

function BackButton(params) {
  return (
    <Grommet theme={CustomizeTheme({
      button: {
        color: 'brand'
      }
    })}>
      <Button label='Back' icon={<FormPreviousLink color='brand' />} onClick={params.onClick} />
    </Grommet>
  );
}

export default BackButton