import React from 'react'
// import logo from './logo.svg';
import './App.css'
import { NavLink } from 'react-router-dom'
import Logo from './global/Logo'
import { Box } from 'grommet'

function Nav() {
  return (
    <Box
      gridArea='sidebar'
      background='#FFFFFF'
    >
      <Box justify='center' align='center'>
        <Logo />
      </Box>

      <Box justify='center' pad={{ top: 'large' }}>
        {[
          // { text: 'Home', href: '/' },
          { text: 'Jobs', href: '/job/abc' },
          { text: 'Post a Job', href: '/post' }
        ].map(link => (
          <NavLink className='side_link' to={link.href}>
            {link.text}
          </NavLink>
        ))}
      </Box>
    </Box>
  );
}

export default Nav;
