import React from 'react'
import { Box, Avatar } from 'grommet'
import { Star } from 'grommet-icons'
import { Grey } from '../global/colors'

function JobViewRecruiterSubSection(params) {
    return (
        <Box fill='horizontal' background='#FFFFFF' gridArea='side' pad='small' margin='small'>
            <Box direction='row' gap='small'>
                <Avatar src={params.recruiter.profile_picture} size='large' />
                <div>
                    <div>{params.recruiter.name}</div>
                    <div style={{
                        fontWeight: 'bold',
                        fontSize: 14,
                        color: Grey,
                    }}>{params.recruiter.title} @ {params.recruiter.organization}</div>
                    <div>
                        {[...Array(params.recruiter.rating)].map(name => (
                            <Star color={Grey} size='small' />
                        ))}
                    </div>
                </div>
            </Box>
        </Box>
    );
}

export default JobViewRecruiterSubSection;
