import React from 'react'
import { Box, Heading } from 'grommet'

import { LightGrey } from '../global/colors'
import Currency from '../global/Currency'


function BenefitsTab(params) {
  return (
    <Box fill background='#FFFFFF' gridArea='main' pad='small' margin='small'>
      <Box pad='small'>
        <Heading size='18px' margin='none' color={LightGrey}>SALARY</Heading>
        <Box direction='row'>
          <Currency currency={params.benefits.salary.currency} />{params.benefits.salary.min} - <Currency currency={params.benefits.salary.currency} />{params.benefits.salary.max} + <Currency currency={params.benefits.salary.currency} />{params.benefits.bonus} bonus
        </Box>
      </Box>
      <Box pad='small'>
        <Heading size='18px' margin='none' color={LightGrey}>REMOTE WORKING</Heading>
        {params.benefits.remoteWorking}
      </Box>
      <Box pad='small'>
        <Heading size='18px' margin='none' color={LightGrey}>BONUS STRUCTURE</Heading>
        {Object.entries(params.benefits.bonusStructure).map(([name, value]) => (
         <div>{value}% {name}</div>
        ))}
      </Box>
      <Box pad='small'>
        <Heading size='18px' margin='none' color={LightGrey}>HOLIDAY</Heading>
        {params.benefits.holiday}
      </Box>
      <Box pad='small'>
        <Heading size='18px' margin='none' color={LightGrey}>EQUITY</Heading>
        {params.benefits.equity}
      </Box>
      <Box pad='small'>
        <Heading size='18px' margin='none' color={LightGrey}>PRIVATE HEALTH CARE</Heading>
        {params.benefits.healthcare}
      </Box>
      <Box pad='small'>
        <Heading size='18px' margin='none' color={LightGrey}>OTHER BENEFITS</Heading>
        {params.benefits.other.map(text => (
          <div>
            {text}
          </div>
        ))}
      </Box>
    </Box>
  );
}

export default BenefitsTab;
