import React, { useState } from 'react'
import { Box, TextInput, RadioButton, MaskedInput } from 'grommet'
import TechTag from './global/tags/TechTag'
import DeletableTechTag from './global/tags/DeletableTechTag'
import ListInput from './ListInput'

function PostBasicInfo(params) {
  const [techStackInput, setTechStackInput] = useState('');
  const onChangeTechStackInput = function (event) {
    if (event.key === 'Enter') {
      let techStack = [...params.techStack]
      techStack.push(techStackInput)
      params.setTechStack(techStack)
      setTechStackInput('')
    }
  }

  const onClickTechStackInput = () => {
    let techStack = [...params.techStack]
    techStack.push(techStackInput)
    params.setTechStack(techStack)
    setTechStackInput('')
  }

  const onDeleteTechStack = function (index) {
    return function (event) {
      let techStack = [...params.techStack]
      techStack.splice(index, 1);
      params.setTechStack(techStack)
    }
  }

  return (
    <div>
      <h3>Position</h3>
      <Box width='medium'>
        <TextInput value={params.position} onChange={event => params.setPosition(event.target.value)} placeholder='Java developer' />
      </Box>
      <h3>Tech Stack</h3>
      <Box width='medium'>
        <ListInput
          value={techStackInput}
          onChange={event => setTechStackInput(event.target.value)}
          onKeyPress={onChangeTechStackInput}
          placeholder='Type a technology'
          iconOnClick={onClickTechStackInput}
        />
      </Box>
      <div hidden>
        Smart suggestions based on other positions like yours<br />
        <TechTag label='Java' /><TechTag label='Spring Framework' /><TechTag label='Postgres' /><br />
      </div>
      {params.techStack && params.techStack.map((text, i) => (
        <DeletableTechTag
          label={text}
          onClick={onDeleteTechStack(i)}
        />
      ))}
      <h3>Employment Type</h3>
      <Box align='center' direction='row'>
        <Box margin='small'>
          <RadioButton
            label='Permanent'
            name='employmentType'
            value='permanent'
            checked={params.employmentType === 'permanent'}
            onChange={event => params.setEmploymentType(event.target.value)}
          />
        </Box>
        <Box margin='small'>
          <RadioButton
            label='Freelance'
            name='employmentType'
            value='freelance'
            checked={params.employmentType === 'freelance'}
            onChange={event => params.setEmploymentType(event.target.value)}
          />
        </Box>
        <Box margin='small'>
          <RadioButton
            label='Part-time'
            name='employmentType'
            value='parttime'
            checked={params.employmentType === 'parttime'}
            onChange={event => params.setEmploymentType(event.target.value)}
          />
        </Box>
        <Box margin='small'>
          <RadioButton
            label='Internship'
            name='employmentType'
            value='internship'
            checked={params.employmentType === 'internship'}
            onChange={event => params.setEmploymentType(event.target.value)}
          />
        </Box>
      </Box>

      <h3>Holiday</h3>
      <Box direction='row' align='center'>
        <Box width='xxsmall'>
          <MaskedInput
            mask={[
              {
                regexp: /^[0-9]{1,2}$/,
                placeholder: "28"
              },
            ]}
            value={params.holiday}
            onChange={event => params.setHoliday(event.target.value)}
          />
        </Box>
        <Box margin={{ left: '8px' }}>
          Days
        </Box>
      </Box>

      <h3>Remote Working</h3>
      <Box align='center' direction='row'>
        <Box margin='small'>
          <RadioButton
            label='Fully'
            name='remote'
            value='fully'
            checked={params.remoteWorking === 'fully'}
            onChange={event => params.setRemoteWorking(event.target.value)}
          />
        </Box>
        <Box margin='small'>
          <RadioButton
            label='Mostly'
            name='remote'
            value='mostly'
            checked={params.remoteWorking === 'mostly'}
            onChange={event => params.setRemoteWorking(event.target.value)}
          />
        </Box>
        <Box margin='small'>
          <RadioButton
            label='Once a week'
            name='remote'
            value='once'
            checked={params.remoteWorking === 'once'}
            onChange={event => params.setRemoteWorking(event.target.value)}
          />
        </Box>
        <Box margin='small'>
          <RadioButton
            label='When needed'
            name='remote'
            value='when'
            checked={params.remoteWorking === 'when'}
            onChange={event => params.setRemoteWorking(event.target.value)}
          />
        </Box>
        <Box margin='small'>
          <RadioButton
            label='Very rarely'
            name='remote'
            value='rarely'
            checked={params.remoteWorking === 'rarely'}
            onChange={event => params.setRemoteWorking(event.target.value)}
          />
        </Box>
        <Box margin='small'>
          <RadioButton
            label='None'
            name='remote'
            value='none'
            checked={params.remoteWorking === 'none'}
            onChange={event => params.setRemoteWorking(event.target.value)}
          />
        </Box>
      </Box>

      <h3>Business Travel</h3>
      <Box align='center' direction='row'>
        <Box margin='small'>
          <RadioButton
            label='100%'
            name='businessTravel'
            value='100'
            checked={params.businessTravel === '100'}
            onChange={event => params.setBusinessTravel(event.target.value)}
          />
        </Box>
        <Box margin='small'>
          <RadioButton
            label='75%'
            name='businessTravel'
            value='75'
            checked={params.businessTravel === '75'}
            onChange={event => params.setBusinessTravel(event.target.value)}
          />
        </Box>
        <Box margin='small'>
          <RadioButton
            label='50%'
            name='businessTravel'
            value='50'
            checked={params.businessTravel === '50'}
            onChange={event => params.setBusinessTravel(event.target.value)}
          />
        </Box>
        <Box margin='small'>
          <RadioButton
            label='25%'
            name='businessTravel'
            value='25'
            checked={params.businessTravel === '25'}
            onChange={event => params.setBusinessTravel(event.target.value)}
          />
        </Box>
        <Box margin='small'>
          <RadioButton
            label='None'
            name='businessTravel'
            value='0'
            checked={params.businessTravel === '0'}
            onChange={event => params.setBusinessTravel(event.target.value)}
          />
        </Box>
      </Box>

    </div >
  );
}

export default PostBasicInfo;