import React from 'react'
import './App.css'
import { BrowserRouter as Router, Route } from 'react-router-dom'
// import Home from './Home'
import Job from './Job'
import Jobs from './Jobs'
import Post from './Post'
import Nav from './Nav'
import { Grommet, Box, Grid } from 'grommet'
import { grommet } from 'grommet/themes'

import { SBOJTheme } from './global/styles'

function App() {
  fetch('https://0jzvzdhkz4.execute-api.eu-west-1.amazonaws.com/prod/testing')
    .then(res => res.json())
    .then(
      (result) => {
        console.log(result)
      })
  return (
    <Router basename={process.env.REACT_APP_ROUTER_BASENAME}>
      <Grommet full theme={{ ...grommet, ...SBOJTheme }}>
        <Grid
          fill
          rows={['full']}
          columns={['250px', 'flex']}
          areas={[
            { name: 'sidebar', start: [0, 0], end: [0, 0] },
            { name: 'main', start: [1, 0], end: [1, 0] }
          ]}
        >
          <Nav />
          <Box gridArea='main' pad='medium' width='xlarge'>
            {/* <Route exact path='/' component={Home} /> */}
            <Route exact path='/' component={Job} />
            <Route exact path='/jobs' component={Jobs} />
            <Route exact path='/post' component={Post} />
            <Route exact path='/job/:id' component={Job} />
          </Box>
        </Grid>
      </Grommet>
    </Router>
  );
}

export default App;
