import React from 'react'
import { Box } from 'grommet'

import { StatusGood } from 'grommet-icons'
import { Turquoise } from '../global/colors'

function Job(params) {
  return (
    <Box fill background='#FFFFFF' gridArea='main' pad='small' margin='small'>
      {params.description}
      <h3 style={{ marginBottom: 8 }}>Job Responsibilities:</h3>
      {params.responsibilities.map(text => (
        <div>
          {text !== '' &&
            <Box pad='small' direction='row' >
              <StatusGood color={Turquoise} size='small' style={{ marginTop: 4 }} />
              <div style={{ marginLeft: 4, lineHeight: 1 }}>{text}</div>
            </Box>
          }
        </div>
      ))}
      <h3 style={{ marginBottom: 8 }}>Required Experience:</h3>
      {params.experiences.map(text => (
        <div>
          {text !== '' &&
            <Box pad='small' direction='row' >
              <StatusGood color={Turquoise} size='small' style={{ marginTop: 4 }} />
              <div style={{ marginLeft: 4, lineHeight: 1 }}>{text}</div>
            </Box>
          }
        </div>
      ))}
      {params.businessTravel && params.businessTravel !== '0' &&
        <h3> Business Travel: {params.businessTravel}%</h3>
      }
    </Box >
  );
}

export default Job;
