import React, { useState } from 'react'
import PropTypes from 'prop-types'
import PostBasicInfo from './PostBasicInfo'
import PostSalaryBenefits from './PostSalaryBenefits'
import PostJobDescription from './PostJobDescription'
import { Box, Tabs, Tab, Button, Grommet, Layer, Image } from 'grommet'
import { CustomizeTheme } from './global/styles'
import { Purple } from './global/colors'
import { css } from 'styled-components'
import { FormNextLink, StatusGood, Send } from 'grommet-icons'
import BackButton from './global/buttons/BackButton'
import { White } from './global/colors'
import Job from './Job'

function isEmpty(str) {
  return (!str || 0 === str.length);
}

function Post({ checked: checkedProp, ...rest }) {

  const [index, setIndex] = React.useState();
  const onActive = nextIndex => setIndex(nextIndex);

  const theme = {
    tab: {
      active: {
        color: 'dark-1'
      },
      color: 'accent-1',
      border: {
        color: 'accent-1'
      },
      size: '250px',
      extend: ({ theme }) => css`
      font-weight: bold;
    `
    },
    tabs: {
      gap: 'medium',
    }
  }

  const [position, setPosition] = useState('')
  const [techStack, setTrackStack] = useState([])
  const [employmentType, setEmploymentType] = useState()
  const [holiday, setHoliday] = useState()
  const [remoteWorking, setRemoteWorking] = useState()
  const [businessTravel, setBusinessTravel] = useState()

  const isBasicInfoComplete = function () {
    return !isEmpty(position) && !isEmpty(techStack) && !isEmpty(employmentType) && !isEmpty(holiday) && !isEmpty(remoteWorking) && !isEmpty(businessTravel)
  }

  const [currency, setCurrency] = useState('')
  const [minSalary, setMinSalary] = useState('')
  const [maxSalary, setMaxSalary] = useState('')
  const [bonus, setBonus] = useState('')
  const [bonusStructure, setBonusStructure] = useState({})
  const [equity, setEquity] = useState()
  const [healthcare, setHealthcare] = useState('None')
  const [otherBenefits, setOtherBenefits] = useState([])

  const isSalaryBenefitsComplete = function () {
    return !isEmpty(currency) && !isEmpty(minSalary) && !isEmpty(maxSalary) && !isEmpty(healthcare)
  }

  const [description, setDescription] = useState('')

  const [responsibility1, setResponsibility1] = useState('')
  const [responsibility2, setResponsibility2] = useState('')
  const [responsibility3, setResponsibility3] = useState('')
  const [responsibility4, setResponsibility4] = useState('')
  const [responsibility5, setResponsibility5] = useState('')

  const [experience1, setExperience1] = useState('')
  const [experience2, setExperience2] = useState('')
  const [experience3, setExperience3] = useState('')
  const [experience4, setExperience4] = useState('')
  const [experience5, setExperience5] = useState('')

  const isJobDescriptionComplete = function () {
    return !isEmpty(description) &&
      (!isEmpty(responsibility1) || !isEmpty(responsibility2) || !isEmpty(responsibility3) || !isEmpty(responsibility4) || !isEmpty(responsibility5)) &&
      (!isEmpty(experience1) || !isEmpty(experience2) || !isEmpty(experience3) || !isEmpty(experience4) || !isEmpty(experience5))
  }

  let job = {
    company: {
      name: 'London Tech',
      website: 'http://www.LonTec.com',
      location: 'London, UK',
      techStack: [
        'Python',
        'Java',
        'Django'
      ],
      elevatorPitch: 'At London Tech, we solve the most complex problems with the most updated solutions.',
      info: 'Since 2010, London Tech, has been consulting with some of the most well known and forward thinking companies in the world. We have built a reputation for delivering complex solution in the simplest and most effective ways to make to that any technical problem is completely solved.',
      size: '+100',
      founded: '2010',
      profile_picture: 'https://i.imgur.com/rK5Jkkc.jpg',
      cover_picture: 'https://i.imgur.com/pazVpu2.jpg',
      socialMedia: {
        linkedin: 'https://www.linkedin.com/company/sboj-io/',
        twitter: 'https://twitter.com/CrosstownDough',
        instagram: 'https://www.instagram.com/crosstowndoughnuts'
      }
    },
    recruiter: {
      name: "You",
      title: 'Head of Recruiting',
      organization: 'Sboj.io',
      rating: 5,
      profile_picture: ''
    },
    benefits: {
      salary: {
        currency: currency,
        min: minSalary,
        max: maxSalary
      },
      remoteWorking: remoteWorking,
      bonus: bonus,
      bonusStructure: bonusStructure,
      equity: equity,
      holiday: holiday,
      healthcare: healthcare,
      other: otherBenefits
    },
    position: position,
    description: description,
    responsibilities: [
      responsibility1,
      responsibility2,
      responsibility3,
      responsibility4,
      responsibility5
    ],
    experiences: [
      experience1,
      experience2,
      experience3,
      experience4,
      experience5

    ],
    employment: employmentType,
    businessTravel: businessTravel,
  }

  const [buttonPublished, setButtonPublished] = useState(false)

  return (
    <Grommet theme={CustomizeTheme(theme)}>
      {buttonPublished && <Layer
        onEsc={() => setButtonPublished(false)}
        onClickOutside={() => setButtonPublished(false)}
      >
        <Box pad='large' width='medium' align='center'>
          <Image
            fill
            fit='cover'
            src={process.env.PUBLIC_URL + '/rocketman.png'}
          />
          <h2 style={{margin: 0}}>SUCCESS!</h2>
          <h3>Your job has been posted.</h3>
          <br /><br />
        Your job should be visible to potential candidates in the next few minutes.
        </Box>

      </Layer>
      }
      <Tabs activeIndex={index} onActive={onActive} justify='left'>
        <Tab title={<RichTabTitle
          isComplete={isBasicInfoComplete()}
          // icon={<Checkmark color='green' />}
          label='1: Basic Info'
        />}>
          <Box fill background='#FFFFFF' gridArea='main' pad='small' margin='small'>
            <PostBasicInfo
              position={position}
              setPosition={setPosition}
              techStack={techStack}
              setTechStack={setTrackStack}
              employmentType={employmentType}
              setEmploymentType={setEmploymentType}
              holiday={holiday}
              setHoliday={setHoliday}
              remoteWorking={remoteWorking}
              setRemoteWorking={setRemoteWorking}
              businessTravel={businessTravel}
              setBusinessTravel={setBusinessTravel}
            />
            <Box direction='row' justify='end'>
              <Box width='small'>
                <Button primary label='Continue' icon={<FormNextLink color={White} />} reverse={true} onClick={() => { setIndex(1) }} />
              </Box>
            </Box>
          </Box>
        </Tab>
        <Tab title={<RichTabTitle
          isComplete={isSalaryBenefitsComplete()}
          // icon={<Checkmark color='green' />}
          label='2: Salary and benefits'
        />}>
          <Box fill background='#FFFFFF' gridArea='main' pad='small' margin='small'>
            <PostSalaryBenefits
              currency={currency}
              setCurrency={setCurrency}
              minSalary={minSalary}
              setMinSalary={setMinSalary}
              maxSalary={maxSalary}
              setMaxSalary={setMaxSalary}
              bonus={bonus}
              setBonus={setBonus}
              bonusStructure={bonusStructure}
              setBonusStructure={setBonusStructure}
              equity={equity}
              setEquity={setEquity}
              healthcare={healthcare}
              setHealthcare={setHealthcare}
              otherBenefits={otherBenefits}
              setOtherBenefits={setOtherBenefits}
            />
            <Box direction='row' justify='end'>
              <Box width='small'>
                <BackButton onClick={() => { setIndex(0) }} />
              </Box>
              <Box width='small'>
                <Button primary label='Continue' icon={<FormNextLink color={White} />} reverse={true} onClick={() => { setIndex(2) }} />
              </Box>
            </Box>
          </Box>
        </Tab>
        <Tab title={<RichTabTitle
          isComplete={isJobDescriptionComplete()}
          label='3: Job Description'
        />}>
          <Box fill background='#FFFFFF' gridArea='main' pad='small' margin='small'>
            <PostJobDescription
              description={description}
              setDescription={setDescription}

              responsibility1={responsibility1}
              setResponsibility1={setResponsibility1}
              responsibility2={responsibility2}
              setResponsibility2={setResponsibility2}
              responsibility3={responsibility3}
              setResponsibility3={setResponsibility3}
              responsibility4={responsibility4}
              setResponsibility4={setResponsibility4}
              responsibility5={responsibility5}
              setResponsibility5={setResponsibility5}

              experience1={experience1}
              setExperience1={setExperience1}
              experience2={experience2}
              setExperience2={setExperience2}
              experience3={experience3}
              setExperience3={setExperience3}
              experience4={experience4}
              setExperience4={setExperience4}
              experience5={experience5}
              setExperience5={setExperience5}
            />
            <Box direction='row' justify='end'>
              <Box width='small'>
                <BackButton onClick={() => { setIndex(1) }} />
              </Box>
              <Box width='small'>
                <Button primary label='Continue' icon={<FormNextLink color={White} />} reverse={true} onClick={() => { setIndex(3) }} />
              </Box>
            </Box>
          </Box>
        </Tab>
        <Tab title={<RichTabTitle
          label='4: Publish'
        />}>
          <Box fill background='#FFFFFF' gridArea='main' pad='small' margin='small'>
            {(!isBasicInfoComplete() || !isSalaryBenefitsComplete() || !isJobDescriptionComplete()) && <div>
              <h3>Hold up, cowboy!</h3>
              <p>We are nearly there, we just need you to add a few more bits of information.</p>
            </div>
            }

            {isBasicInfoComplete() && isSalaryBenefitsComplete() && isJobDescriptionComplete() && <div>
              <h3>Ready to post?</h3>
              <p>You are almost there. All that is left is to review the preview of your job posting below and then click publish.</p>
            </div>}

            <div hidden><pre>{JSON.stringify({ job }, null, 2)}</pre></div>
            <Box direction='row' justify='end'>
              <Box width='small'>
                <BackButton onClick={() => { setIndex(2) }} />
              </Box>
              <Box width='small'>
                <Button
                  disabled={!(isBasicInfoComplete() && isSalaryBenefitsComplete() && isJobDescriptionComplete())}
                  primary
                  label='Publish'
                  icon={<Send color={White} />}
                  reverse={true}
                  onClick={() => { setButtonPublished(true) }}
                />
              </Box>
            </Box>
          </Box>
          {isBasicInfoComplete() && isSalaryBenefitsComplete() && isJobDescriptionComplete() && <Job job={job} hideApplyButton />}

        </Tab>
      </Tabs>


    </Grommet>
  );
}

export default Post;

const RichTabTitle = ({ label, isComplete }) => (
  <div>
    {!isComplete &&
      <Box direction='row' align='center' gap='xsmall' pad={{ right: 'large' }}>
        <span style={{ color: Purple }}>{label}</span>
      </Box>
    }
    {
      isComplete &&
      <Box direction='row' align='center' gap='xsmall' pad={{ right: 'large' }}>
        <span style={{ color: Purple }}>{label}</span>
        <StatusGood color='green' size='medium' />
      </Box>
    }
  </div>
);

RichTabTitle.propTypes = {
  label: PropTypes.string.isRequired
};